	module.exports = {"data":{"wordpressPost":{"title":"Poorly understood postural syndrome blights lives of young well educated women","content":"<p><a href=\"http://bmjopen.bmj.com/content/4/6/e004127.full\">A debilitating syndrome that causes an excessively rapid heartbeat on standing up, predominantly affects young well educated women, and blights their lives, because it is so poorly understood and inconsistently treated</a>, reveals a small study published in the online journal<a href=\"http://bmjopen.bmj.com/\"> BMJ Open</a>.</p>\n<p>Postural tachycardia syndrome, or PoTS for short, is a by-product of orthostatic intolerance &#8211; a disorder of the autonomic nervous system in which the circulatory and nervous system responses needed to compensate for the stress put on the body on standing upright, don’t work properly.</p>\n<p>PoTS is associated with an excessively rapid heartbeat, or tachycardia. Symptoms include dizziness, fainting, nausea, poor concentration, excessive fatigue and trembling, and can be so severe as to make routine activities, such as eating and bathing, very difficult to do.</p>\n<p>The impact of the syndrome has been likened to the level of disability associated with serious and debilitating long term conditions, such as chronic obstructive pulmonary disease (COPD) and congestive heart failure.</p>\n<p>In the US, PoTS is thought to affect around 170 per 100,000 of the population, one in four of whom is disabled and unable to work.</p>\n<p>But the symptoms, and their impact, are frequently not recognised in the UK, or attributed to anxiety, panic disorder, or chronic fatigue syndrome (CFS), say the researchers, who wanted to find out if PoTS affects particular groups, and how.</p>\n<p>They therefore assessed 84 members of the national charity and support group, PoTS UK, and 52 patients diagnosed with the syndrome at the NHS falls and syncope clinic in Newcastle, north east England, between 2009 and 2012.</p>\n<p>All participants completed a validated set of questionnaires specifically aimed at gauging levels of fatigue; sleepiness; orthostatic intolerance; anxiety and depression; ability to carry out routine tasks; and brain power.</p>\n<p>The profile of the two groups was broadly similar, and indicated that people with PoTS are predominantly young &#8211; average age of diagnosis 30-33 &#8211; well educated to degree or postgraduate degree level, and female.</p>\n<p>Poor health had prompted a significant number to change their jobs or give up working altogether, and both groups experienced high levels of fatigue, daytime sleepiness, orthostatic symptoms, anxiety and depression, memory and concentration problems, and considerable difficulty carrying out routine tasks.</p>\n<p>Around one in five people had been diagnosed with CFS and a similar proportion had Ehlers-Danlos syndrome (inherited connective tissue disorders), suggesting that there may be an underlying overlapping cause, say the researchers.</p>\n<p>Beta blocker drugs, which regulate heart rate, were the most common treatment for PoTS. But altogether, patients reported taking 21 different combinations of drugs. And a significant number were taking nothing at all or just salt.</p>\n<p>“Patients with PoTS &#8230; have significant and debilitating symptoms that impact significantly on their quality of life,” write the researchers. “Despite this, there is no consistent treatment, high levels of disability, and associated comorbidity.”</p>\n<p>They go on to emphasise that their findings indicate that patients with PoTS experience a similar level of disability to people with CFS, but yet don’t receive the same protection in law. “Our experience suggests that some patients never recover, and that a subset will worsen over time,” they conclude.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"fc202443-7685-59f9-8b2e-dcff3302fc67"}}

